.jumbo-container {
  position: relative;
  overflow: hidden;
}
.jumbo {
  height: 351px;
  left: 165px;
  top: 227px;
  border-radius: 0px;
  margin-bottom: 250px;
  .slogan {
    color: #34313d;
    font-family: Poppins;
    font-weight: 700;
    font-size: rem(80px);
    left: 0px;
    top: 0px;
    border-radius: nullpx;
    line-height: 90px;
  }
  .short-explanation {
    font-family: Poppins;
    font-size: 22px;
    font-style: normal;
    font-weight: 500;
    line-height: 36px;
    letter-spacing: 0.15000000596046448px;
    text-align: left;
    color: #9e9aa8;
  }
  .jumbo-btn {
    height: 56px;
    width: 197px;
    left: 0px;
    top: 295px;
    background-color: #2bd0d0;
    color: #fff;
    border-radius: 28px;
    border-color: #fff;
    &:active {
      background-color: #9ae3e3;
    }
    span {
      font-family: Poppins;
      font-weight: 700;
      font-size: rem(20px);
      line-height: 30px;
      height: 30px;
      width: 117px;
      left: 40px;
      top: 14px;
    }
  }
}
.jumbo-img {
  position: absolute;
  right: -35px;
  overflow: hidden !important;
}

@media (max-width: 800px) {
  .jumbo {
    display: flex;
    margin-top: 250px;
  }
  .slogan {
    text-align: center !important;
    font-size: rem(42px) !important;
    line-height: 48px !important;
    letter-spacing: -1.05px;
    width: 100%;
    margin-top: 170px;
    display: flex;
    flex-direction: column;
  }
  .jumbo-img {
    position: absolute;
    top: 45px;
    z-index: 1;
  }
  .short-explanation {
    font-size: rem(18px) !important;
    line-height: 30px !important;
    text-align: center !important;
    letter-spacing: 0.122727px;
    max-width: 30ch !important;
    margin: 15px 40px 0px 40px;
  }
  .jumbo-btn-small-screen {
    margin-top: 32px;
    display: flex;
    align-self: center;
    justify-self: center;
  }
}
