.stat {
  min-height: 800px;
  background-color: #eff1f7;
  padding-bottom: 120px;
}
.st-text {
  font-family: Poppins;
  height: 130px;
  width: 540px;
  left: 450px;
  top: 1004px;
  border-radius: 0px;
  margin: 204px auto 56px auto;

  .ad-st {
    text-align: center;
    font-size: rem(40px);
    font-weight: 700;
    line-height: 48px;
    margin-bottom: 18px;
  }
  .ad-p {
    margin: 0 auto;
    max-width: 50ch;
    height: 64px;
    left: 0px;
    top: 66px;
    border-radius: nullpx;
    color: #9e9aa8;
    font-weight: 500;
    font-size: rem(18px);
    text-align: center;
    line-height: 32px;
    letter-spacing: 0.12px;
  }
}
.green-line {
  margin-top: 125px;
  width: 30px;
  height: 8px;
  background-color: #2bd0d0;
}

.st-cards {
  z-index: 1;
  display: grid;
  grid-template-columns: 1fr 30px 1fr 30px 1fr;
  height: 399px;
  // width: 1110px;
  overflow: hidden;
  > *:not(.green-line) {
    z-index: 2;
    border-radius: 0px;
    height: 311px;
    // width: 350px;
    background-color: #fff;
  }

  .st-card1 {
    margin-bottom: 88px;
  }
  .st-card2 {
    margin-bottom: 44px;
    margin-top: 44px;
  }
  .st-card3 {
    margin-top: 88px;
  }

  .st-card-img {
    height: 48px;
    width: 47.46763610839844px;
  }

  .oval {
    margin-top: -44px;
    width: 88px;
    height: 88px;
    background-color: $clr2;
    position: absolute;
    border-radius: 50%;
  }
  .st-txt {
    margin-left: 32px;
    margin-top: 77px;
    font-family: Poppins;
  }
  .txt1-st {
    color: #34313d;
    font-size: 22px;
    font-weight: 700;
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;
    text-align: left;
  }

  .txt2-st {
    margin-top: 5px;
    width: 286px;
    height: 104px;
    color: #9e9aa8;
    font-size: 15px;
    font-weight: 500;
    font-style: normal;
    letter-spacing: normal;
    line-height: 26px;
    text-align: left;
  }
}

@media (max-width: 450px) {
  .st-text {
    width: 100%;
    h2 {
      font-size: rem(28px) !important;
    }
    .ad-p {
      font-size: rem(16px) !important;
      margin: 0px 25px 0px 25px;
    }
  }
  .st-cards {
    margin-top: 48px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    >*{
      margin-top: 0px !important;
      margin-bottom: 42px !important;
    }
    .green-line{
      transform: rotate(90deg);
      margin: 0 auto;
      width:110px;
    }
    .oval {
      left: 39%;
      margin-top: -44px;
      width: 88px;
      height: 88px;
      background-color: $clr2;
      position: absolute;
      border-radius: 50%;
    }
  }
}
