.main-header {
  font-family: RedHatB;
  margin-bottom: 146px;
  margin-top: 40px;
  height: 80px;
  text-align: center;
  flex-wrap: wrap;
  & > * {
    display: flex;
    color: $clr1;
  }

  .logo {
    margin-right: rem(64px);
    color: $clr1;
    font-size: rem(40px);
  }
  .left-nav {
    font-size: rem(16px);
    color: $clr4;
    width: 25%;
    & > * {
      margin-left: rem(20px);
    }
  }
  .main-nav {
    width: 75%;
    flex-wrap: wrap;
  }
  .center-nav {
    justify-content: flex-start;
    & > * {
      margin-right: 30px;
    }
    flex-wrap: wrap;
    flex-grow: 2;
  }

  .right-nav {
    font-size: rem(16px);
    color: $clr4;
    flex-grow: 1;
    justify-content: flex-end;
    & > * {
      margin-left: rem(10px);
      margin-right: rem(10px);
    }
    .signup {
      display: flex;
      justify-content: center;
      align-items: center;
      align-self: center;
      height: 40px;
      width: 112px;
      left: 81px;
      top: 0px;
      border-radius: 0px;
      background-color: #2bd0d0;
      border-radius: 40px;
      color: #fff;
      font-family: RedHatM;
      font-size: rem(16px);
      &:active {
        background-color: #9ae3e3;
      }
    }
  }
  .mobile-menu {
    display: none;
  }
}
@media (max-width: 700px) {
  .main-header {
    margin-bottom: 0px;
    .logo {
      align-self: flex-start;
      margin-right: rem(64px);
      color: $clr1;
      font-size: rem(40px);
    }
  }
}

@media (max-width: 450px) {
  .main-nav {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    right: -340px;
    height: 383px;
    width: 90% !important;
    background-color: #3A3054;
    opacity: 0.9;
    z-index: 2;
    transform: translateX(0%);
    transition: transform 0.8s;
    top: 96px;
  }
  .center-nav {
    color:#fff !important;
    width: 90%;
    justify-content: space-between !important;
    display: flex;
    flex-direction: column;
    margin-top: 40px;
    flex-grow: 2;
    &>*{
      width:100%;
    }
  }
  .center-nav :last-child{
    padding-bottom: 30px;
    border-bottom: 1px solid #9E9AA8;
  }
  .right-nav{
    padding-top: 32px;
    padding-bottom: 40px;
    flex-grow: 2 !important;
    color:#fff !important;
    display: flex;
    flex-direction: column;
    align-items: unset !important;
    width:90%;
    &:first-child{
      padding-top: 32px;
    }
    .signup{
      width:90% !important;
    }
  }
  .mobile-menu {
    display: inline-block !important;
    position: absolute;
    top: 20px;
    right: 26px;
    font-size: 24px;
  }
  .menu-open {
    transform: translateX(-105%);
    opacity: 1;
    visibility: visible;
  }
}
