.search-bar {
  position: absolute;
  width: 50%;
  height: 161px;
  left: 25%;
  top: 780px;
  background: #3a3054;
  border-radius: 10px;
  flex-wrap: wrap;
  img {
    position: absolute;
    height: 100%;
    overflow: hidden;
    z-index: 0;
    border-radius: 10px;
  }
  > *:not(img) {
    z-index: 1;
  }
  input {
    margin-left: 64px;
    flex-grow: 2;
  }
  .error-input {
    margin-left: 64px;
    flex-grow: 2;
    margin: 10px;
    padding: 17px 41px;
    border-radius: 10px;
    border: solid red;
    font-weight: 500;
    background-color: rgb(240, 235, 235);
    font-family: montserrat;
    font-size: rem(18px);
    &::-webkit-input-placeholder {
      color: red;
    }
    &:focus {
      outline: none;
    }
    color: red;
  }
  .search-btn {
    margin-left: 24px;
    margin-right: 64px;
    padding: 14px 41px;
    font-family: Poppins;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 30px;
    color: #ffffff;
    background: #2bd0d0;
    border-radius: 10px;
    border-color: none;
  }
  .error {
    display: flex;
    justify-content: flex-start;
    align-self: start;
    line-height: 0%;
    color: red;
    width: 100%;
    margin-left: 64px;
  }
}

.search-res {
  margin-top: 150px;
  font-family: Poppins;
  .res {
    padding-left: 32px;
    height: 72px;
    background-color: #fff;
    margin-bottom: 16px;
    .res-1 {
      width: 60%;
      font-weight: 500;
      font-size: 20px;
      line-height: 36px;
      color: #34313d;
      letter-spacing: 0.15px;
    }
    .res-2 {
      width: 40%;
      font-weight: 500;
      font-size: 20px;
      line-height: 36px;
      text-align: right;
      letter-spacing: 0.15px;
      color: #2bd0d0;

      .btn1 {
        font-weight: bold;
        font-size: 15px;
        line-height: 22px;
        color: #fff;
        background: #2bd0d0;
        padding: 10px 31px;
        border-radius: 5px;
        &:active {
          background-color: #9ae3e3;
        }
      }
      .btn2 {
        font-weight: bold;
        font-size: 15px;
        line-height: 22px;
        color: #fff;
        background: #3a3054;
        padding: 10px 31px;
        border-radius: 5px;
      }
    }
  }
}

@media (max-width: 1250px) {
  .search-bar {
    input {
      margin: 8px;
    }
    .error-input {
      margin: 8px;
    }
    .search-btn {
      margin:8px;
      padding: 8px 17px;
      font-size: rem(14px);
      line-height: 24px;
    }
  }
}

@media (max-width: 600px) {
  .search-bar {
    width:80%;
    left:10%;
    input {
      flex-grow: unset;
      width:80% !important;
      margin: 8px;
    }
    .error-input {
      flex-grow: unset;
      width: 80% !important;
      margin: 8px;
      padding:10px 20px ;
      font-size: rem(14px);
      &::-webkit-input-placeholder{
        text-align: left;
      }
    }
    .search-btn {
      width:80%;
      margin:8px;
      padding: 8px 17px;
      font-size: rem(12px);
      line-height: 24px;
    }
  }
}
