.input{
    margin: 10px;
    padding:17px 41px;
    border-radius: 10px;
    font-weight: 500;
    background-color: rgb(240, 235, 235);
    font-family: montserrat;
    font-size: rem(18px);

    &:focus{
        outline: none;
    }
}

@media(max-width:1220px){
    .input{
        margin: 10px;
        padding:10px 20px;
        border-radius: 6px;
        font-weight: 500;
        background-color: rgb(240, 235, 235);
        font-family: montserrat;
        font-size: rem(14px);
    
    }
}