.footer-container {
  background-color: #232127;
  color: #fff;
  .footer-p {
    padding: rem(71px) rem(164px) rem(71px) rem(164px);
  }

  .footer-logo {
    padding-right: rem(260px);
    h2 {
      color: #fff;
    }
  }

  .footer {
    flex-grow: 3;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 10px;
    .foot-col {
      font-display: flex;
      flex-direction: column;
      text-align: left;
    }
    p {
      cursor: pointer;
      display: block;
      font-size: rem(15px);
      &:active {
        color: #2bd0d0;
      }
    }
    .bold {
      font-size: rem(16px);
      font-weight: 900;
      margin-bottom: 20px;
    }
    .icon {
      cursor: pointer;
      &:active {
        color: #2bd0d0;
      }
    }
  }
}

@media (max-width: 1050px) {
  .footer-container {
    .footer-p {
      display: flex;
      flex-direction: column;
      padding: 25px 30px;
    }
    .footer-logo {
      padding-right: rem(260px);
      h2 {
        color: #fff;
      }
    }
  }
}

@media (max-width: 450px) {
  .footer-container {
    display: flex;
    align-items: center;
    justify-content: center;
    .footer-p {
      padding: 25px 30px;
    }
    .footer-logo {
     margin:54px auto;
     padding: 0px !important;
      h2 {
        text-align: center;
      }
    }
    .footer{
      display: flex !important;
      flex-direction: column;
      >*{
        margin-bottom: 40px;
        p{
          text-align: center;
        }
      }
    }
  }
}
