.boost-container {
  height: 250px;
  background-color: $clr2;
  img {
    position: absolute;
    height: fit-content;
    z-index: 0;
  }
  >*:not(img){
      z-index: 1;
  }
  h2{
    font-family: Poppins;
      color:#fff;
  }
  .jumbo-btn {
    height: 56px;
    width: 197px;
    background-color: #2bd0d0;
    color: #fff;
    border-radius: 28px;
    border-color: none;
    &:active{
      background-color: #9AE3E3;
    }
     span {
      font-family: Poppins;
      font-weight: 700;
      font-size: rem(20px);
      line-height: 30px;
      height: 30px;
      width: 117px;
    }
  }
}

@media(max-width:450px){
  .boost-container {
    display: flex;
    align-items: center;
    justify-items: center;
    height: 250px;
    background-color: $clr2;
    img {
      position: absolute;
      height: fit-content;
      z-index: 0;
    }
    >*:not(img){
        z-index: 1;
    }
    h2{
        color:#fff;
        text-align: center;
        font-size: 28px !important;
        line-height: 48px !important; 
        letter-spacing: -0.7px;
    }
    .jumbo-btn {
      height: 56px;
      width: 197px;
      background-color: #2bd0d0;
      color: #fff;
      border-radius: 28px;
      border-color: none;
      &:active{
        background-color: #9AE3E3;
      }
       span {
        font-family: Poppins;
        font-weight: 700;
        font-size: rem(20px);
        line-height: 30px;
        height: 30px;
        width: 117px;
      }
    }
  }
  
}